import axios from 'axios'
import {request} from './request'

// 
export function getProvince() {
  return request({
    url: '/region/province',
    method: 'get'
  })
}

// 
export function getCity(pid) {
  return request({
    url: '/region/city',
    method: 'get',
    params: {
      parent_id: pid
    }
  })
}


// 
export function getDistrict(pid) {
  return request({
    url: '/region/district',
    method: 'get',
    params: {
      parent_id: pid
    }
  })
}

