        ],
        demand_experience: [
          { required: true, message: "", trigger: "blur" },
        ],
        demand_census: [
          { required: true, message: "", trigger: "blur" },
        ],
        demand_service_skill: [
          { required: true, message: "", trigger: "blur" },
        ],
        demand_sex: [
          { required: true, message: "", trigger: "blur" },
