import {
  request
} from './request'

// 
export function searchStaffNmae(keyword) {
  return request({
    url: "/staff/waiting",
    method: "get",
    params: {
      keyword: keyword
    }
  })
}


/**
 * 
 */
// 
export function getCustomerInfo() {
  return request({
    url: "/customer/index",
    method: "get",
  })
}
// 
export function saveCustomerInfo(data) {
  return request({
    url: "/customer/save",
    method: "post",
    data: data
  })
}
// 
export function getOneCustomerInfo(id) {
  return request({
    url: "/customer/edit",
    method: "get",
    params: {
      id: id
    }
  })
}
// 
export function deleteCustomer(id) {
  return request({
    url: "/customer/delete",
    method: "get",
    params: {
      id: id
    }
  })
}
// 
export function updateCustomer(data) {
  return request({
    url: "/customer/update",
    method: "post",
    data: data
  })
}



